import edsWarning, {
    getElementNotFoundWarning,
    getInvalidSelectorWarning
} from './eds-warning';

export const queryCheck = selector =>
    document.createDocumentFragment().querySelector(selector);

export const isSelectorValid = selector => {
    try {
        queryCheck(selector);
    } catch {
        return false;
    }
    return true;
};

export const edsQuerySelector = selector => {
    if (isSelectorValid(selector)) {
        return (
            document.querySelector(selector) ||
            edsWarning(getElementNotFoundWarning(selector))
        );
    }
    edsWarning(getInvalidSelectorWarning(selector));
    return false;
};

export default edsQuerySelector;
