import warning from 'warning';

export const getElementNotFoundWarning = selector =>
    `${selector} element was not found in the DOM.`;

export const getInvalidSelectorWarning = selector =>
    `${selector} is not a valid selector.`;

const warned = {};

export default function edsWarning(message) {
    if (!warned[message]) {
        warning(false, message);
        warned[message] = true;
    }
}
