/* eslint-disable import/prefer-default-export */
export const getListVariant = (item, groupNameKey, categoryNameKey) => {
    if (item[groupNameKey]) return 'group';
    if (item[categoryNameKey]) return 'category';
    return 'standard';
};

export const getRole = ({
    nonInteractive,
    leadingBlockType,
    trailingBlockType,
    role
}) => {
    if (nonInteractive) return undefined;
    if (
        leadingBlockType === 'checkbox' ||
        trailingBlockType === 'checkbox' ||
        leadingBlockType === 'switch' ||
        trailingBlockType === 'switch'
    )
        return 'group';
    if (leadingBlockType === 'radio' || trailingBlockType === 'radio')
        return 'radiogroup';
    return role;
};

export const getSelectedItemIndex = (
    groupIndex,
    categoryIndex,
    roleToUse,
    selected,
    singleList
) => {
    if (roleToUse === 'group' || Array.isArray(selected)) {
        const selectedIndexes = [];
        // eslint-disable-next-line consistent-return
        selected?.forEach(s => {
            if (s.selectedCategoryIndex || s.selectedCategoryIndex === 0) {
                if (
                    s.selectedCategoryIndex === categoryIndex &&
                    s.selectedGroupIndex === groupIndex
                ) {
                    selectedIndexes.push(s.selectedIndex);
                }
            } else if (s.selectedGroupIndex === groupIndex) {
                selectedIndexes.push(s.selectedIndex);
            }
        });
        return selectedIndexes;
    }

    if (
        selected?.selectedCategoryIndex ||
        selected?.selectedCategoryIndex === 0
    ) {
        if (
            selected?.selectedCategoryIndex === categoryIndex &&
            selected?.selectedGroupIndex === groupIndex
        ) {
            return selected?.selectedIndex;
        }
        return -1;
    }

    if (singleList && selected?.selectedGroupIndex === groupIndex) {
        return selected?.selectedIndex;
    }

    if (!singleList && selected?.selectedGroupIndex === groupIndex) {
        return selected?.selectedIndex;
    }

    return -1;
};

export const isInViewport = element => {
    const rect = element.getBoundingClientRect();
    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <=
            (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <=
            (window.innerWidth || document.documentElement.clientWidth)
    );
};
